
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

export default {
name: "getGoods",
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
Cell,
Group,
XInput,
PopupPicker,
XButton,
Box,
XAddress,
ChinaAddressV4Data,
Checker,
CheckerItem
},
created() {
this.queryOrder();
if (JSON.stringify(this.$route.params) === "{}") {
this.queryUserAddress();
} else {
this.getGoodsObj.addressId = this.$route.params.addressId;
this.getGoodsObj.addressName = this.$route.params.addressName;
}
this.queryUser();
},
computed: {
payAmount: function () {
let price = 0;
if (this.checkerWhich === 2) {
if (this.getGoodsObj.quantity >= 20 && this.getGoodsObj.quantity < 100) {
return this.getGoodsObj.quantity * 3 * 100
} else if (this.getGoodsObj.quantity >= 100) {
return this.getGoodsObj.quantity * 3 * 80
} else {
return this.getGoodsObj.quantity * 3 * 120
}
} else {
if (this.getGoodsObj.quantity >= 60) {
this.$vux.toast.text("超过60件建议按箱拿货", "top");
}
return this.getGoodsObj.quantity  * 120
}
}
},
data() {
return {
index: 0,
checkerWhich: 2,
userInfo: {},
getGoodsObj: {
quantity: "",
addressId: "",
proxyProvice: "",
proxyCity: "",
proxyArea: "",
addressName: "",
addressValue: []
},
orderList: [],
addressData: ChinaAddressV4Data,
res: {}
};
},
methods: {
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryUserAddress() {
this.$api.queryUserAddress({}).then(data => {
if (data.code === 200) {
if (data.data.address.length < 1) {
this.getGoodsObj.addressName = '无'
} else {
this.getGoodsObj.addressId = data.data.address[0].id;
this.getGoodsObj.addressName =
data.data.address[0].province +
data.data.address[0].city +
data.data.address[0].area +
data.data.address[0].detail;
}
}
});
},
queryOrder() {
this.$api
.queryOrder({
pageNo: 1,
pageSize: 100
})
.then(data => {
if (data.code === 200) {
this.orderList = data.data.items;
}
});
},
wechatCallPay() {
this.$api
.wechatCallPay({
totalFee: this.payAmount * 100
})
.then(data => {
if (data.code === 200) {
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
}
}else{
this.onBridgeReady(data.data.appid, data.data.nonce_str, data.data.prepay_id, data.data.sign)
}
}
});
},
cancelOrder(id) {
this.$api
.cancelOrder({
id: id
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("取消订单成功", "top");
this.queryOrder();
}
});
},
insertOrder() {
let insertObj = {};
if (!this.getGoodsObj.addressId) {
return this.$vux.toast.text("请添加地址后再拿货~", "top");
}
insertObj.addressId = this.getGoodsObj.addressId;
insertObj.paidFee = this.payAmount;
if (this.checkerWhich == 2) {
insertObj.quantity = this.getGoodsObj.quantity * 3;
} else {
insertObj.quantity = this.getGoodsObj.quantity;
}
console.log(insertObj);
return
this.$api.insertOrder(insertObj).then(data => {
if (data.code === 200) {
this.$vux.toast.text("添加订单成功", "top");
this.queryOrder();
this.index = 1;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
toSelectAddress() {
this.$router.push({
name: "selectAddress",
params: {
addressId: this.getGoodsObj.addressId,
addressName: this.getGoodsObj.addressName
}
});
},
toSale() {
this.$router.push({
name: "sales"
});
},
selectChecker(val) {
},
onBridgeReady(appId, nonceStr, prepay_id, paySign){
let timeStamp = Date.parse(new Date()) / 1000;
console.log(timeStamp);

WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId": appId,     //公众号名称，由商户传入
"timeStamp": timeStamp,         //时间戳，自1970年以来的秒数
"nonceStr": nonceStr, //随机串
"package": "prepay_id=" + prepay_id,
"signType": "MD5",         //微信签名方式：
"paySign": paySign //微信签名
},
function(res){
this.res = JSON.stringify(res);
alert(this.res)
if(res.err_msg == "get_brand_wcpay_request:ok" ){


}
}
);
}
}
};



